#card-container {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
}

.card {
  background-color: #0076d1; /* follow water css style */
  color: white;
  border-radius: 10px;
  font-size: 0.7em;
  padding: 5%;
  margin: 2%;
}

.card:hover {  /* consider the link in notes week8 */
  transform: scale(1.03);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 150, 191, 0.67); /* follow water css style */
}

.song-image {
  width: 100%;
  border-radius: 5px;
}

.card-title {
  color: white;
  margin: 10px 5px;
}

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  font-size: 1em;
  color: #fff; /* follow water css style */
}

.card-footer time,
.card-footer .duration {
  padding: 3px 8px;
  border-radius: 6px;
}

.card-footer .lang {
  font-size: 0.8em;
  margin: 2px;
  padding: 0;
}
